.formContainer{
    display: flex;
    align-items: center;
    justify-content: center;
    align-content: center;
    margin-top: 5%;
  }
  
  
  
  .form {
    background-color: #0e0e0ee6;
    border-radius: 10px;
    padding: 20px;
    width: 350px;
    margin-top: 10px;
  }
    
  .lb {
    display: block;
    margin-bottom: 10px;
    font-size: 18px;
    font-weight: bold;
    color: white
  }
    
  .infos[type="name"], input[type="difucultad"], input[type="dificultad"] {
    width: 100%;
    padding: 10px;
    font-size: 16px;
    border-radius: 5px;
    border: none;
    margin-bottom: 20px;
    background-color: #333333;
    color: white;
  }
    
  .addText{
    color: white
  }
  
  .alertConfirm{
    background-color: rgba(249, 1, 1, 0.558);
    color: whitesmoke;
  }
  
  .paisId{
    color: white
  }
  
  .countriesList{
    display: flex;
    align-items: center; 
    justify-content: center; 
    color: red;
    margin-top: 2px;
    margin-bottom: 2px;
  }
    
  .error {
    color: red;
    align-items: center;
    align-content: center;
    justify-content: center;
  }
    
  .buttons{
    gap: 0.5rem;
  }
  
  .btnX {  
    display: flex;
    align-items: center; 
    padding: 5px 1rem;
    margin-top: 15px;
    margin-left: 10px;
    background-color: red;
    color: black;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    border-radius: 100rem;
    transition: 0.2s;
  
  }
    
  .btnX:hover {
    color: white;
    outline-offset: 4.5px;
    background: orange;
  } 
    
  .send {
    --glow-color: rgb(176, 255, 189);
    --glow-spread-color: rgba(123, 255, 160, 0.781);
    --enhanced-glow-color: rgb(182, 175, 71);
    --btn-color: rgba(13, 241, 21, 0.508);
    border: 0.2em solid var(--glow-color);
    padding: 1em 2em;
    color: var(--glow-color);
    font-size: 14px;
    font-weight: bold;
    background-color: var(--btn-color);
    border-radius: 1em;
    outline: none;
    box-shadow: 0 0 1em 0.25em var(--glow-color),
                0 0 4em 1em var(--glow-spread-color),
                inset 0 0 0.05em 0.25em var(--glow-color);
    text-shadow: 0 0 0.5em var(--glow-color);
    position: relative;
    transition: all 0.3s;
    z-index: 1;
    margin-right: 0.5rem; 
  }
    
  .send::after {
    pointer-events: none;
    content: "";
    position: absolute;
    top: 120%;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: var(--glow-spread-color);
    filter: blur(2em);
    opacity: 0.7;
    transform: perspective(1.5em) rotateX(35deg) scale(1, 0.6);
    z-index: -1;
    margin-right: 0.5rem; 
  }
    
  .send:hover {
    color: var(--glow-color);
    background-color: var(--btn-color);
    box-shadow: 0 0 1em 0.25em var(--glow-color),
                0 0 4em 2em var(--glow-spread-color),
                inset 0 0 0.75em 0.25em var(--glow-color);
    transform: translateY(-0.1em);
    margin-right: 0.5rem; 
  }