.landingContainer{
    display: flex;
    align-items: center;
    justify-content: center;
    align-content: center;
    width: 100%;
    height: 100%;
    background-color: #478a47;
    flex-direction: column; 
  }
  
  .mapa{
    width: 200px;
    height: 200px;
    border-radius: 50%;
    box-shadow: -30px -20px 0 0 rgba(153, 153, 153, 0.186) inset,
                -70px -30px 0 0 rgba(0,0,0, .1) inset,
                5px 5px 0 0 rgba(255, 255, 255, 0.1) inset;
    background: url("../../img/mapa2.png");
    background-size: cover;
    animation: tierra-girando 5s infinite linear;
    -webkit-animation: tierra-girando 10s infinite linear;
    }
  
    @keyframes tierra-girando{
      from{
        background-position: left-center;
        } to {
          background-position: -396px center;
          }
    }
  
  .titleLanding {
    color: black;
    background: #fffc00;
    background: -webkit-linear-gradient(to right, #F9F54B, #f9f8a4);
    background: linear-gradient(to right, #F9F54B,#f9f8a4 );
    border-radius: 1rem;
    width:35%;
    margin-top: -80px;
    box-shadow: 0px 10px 40px 0px yellow;
    z-index: 9999;
  }
      
    
  .h3 {
    color: white;
    background: #000000;
    background: -webkit-linear-gradient(to right, #000000, #434343);
    background: linear-gradient(to right, #000000, #434343);
    border-radius: 0.5rem;
    width: 20%;
    margin-top: 2%;
    text-align: center;
  }
    
  
  .buttonLanding {
    background: #3F979B;
    padding: 10px 2rem;
    border: 0;
    color: black;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 1rem;
    border-radius: 100rem;
    transition: 0.2s;
    font-weight: 600;
    cursor: url("../../img/pointeravion.png"), pointer; 
  
  } 
    
  .buttonLanding:hover {
    outline-offset: 4.5px;
    background: #FFED00;    
    z-index: 9999;
  }
  
  .footer{
    position: absolute;
    width: 100%;
    bottom: 0;
    left: 0;
    background-color: yellow;
  }
  
  
  @media screen and (max-width: 768px) {
    footer {
      flex-direction: column;
    }
  }