.NavBar {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    align-content: center;
    justify-content: space-around;
    background-color: #3caebdf3;
    width: 100%;
    height: 100%;
}

img {
    vertical-align: middle;
}

.link { 
    display: flex;
    align-items: center;
    background: #3F979B;  
    vertical-align: middle;
    color: black;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 1rem;
    border-radius: 100rem;
    transition: 0.2s;
    font-weight: 600;
    cursor:  pointer; 
  } 
  
  .link:hover {
    outline-offset: 4.5px;
    background: #FFED00;    
 }
